<template>
  <div id="view-student">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{ nama }}</h4>
      </li>
      <li class="collection-item">nim : {{ nim }}</li>
      <li class="collection-item">jurusan : {{ jurusan }}</li>
    </ul>
    <router-link v-bind:to="{ path: '/' }">back</router-link>
    <a href="/" class="btn grey">back</a>
    <button v-on:click="deleteStudent" class="btn red ml">Delete</button>
    <div class="fixed-action-btn">
      <router-link
        v-bind:to="{ name: 'edit-student', params: { nim: nim } }"
        class="btn-floating btn-large red"
      >
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "view-student",
  data() {
    return {
      nim: null,
      nama: null,
      jurusan: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("student")
      .where("nim", "==", to.params.nim)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.nim = doc.data().nim;
            vm.nama = doc.data().nama;
            vm.jurusan = doc.data().jurusan;
          });
        });
      });
  },
  // fetch data dengan watcher
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("student")
        .where("nim", "==", this.$route.params.nim)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.nim = doc.data().nim;
            this.jurusan = doc.data().jurusan;
            this.nama = doc.data().nama;
          });
        });
    },
    deleteStudent() {
      if (confirm("yakin ingin mengapus ?")) {
        db.collection("student")
          .where("nim", "==", this.$route.params.nim)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete();
              this.$router.push("/");
            });
          });
      }
    }
  }
};
</script>

<style scoped>
.ml {
  margin-left: 10px;
}
</style>
