<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Student</h4></li>
      <li
        v-bind:key="student.id"
        v-for="student in students"
        class="collection-item"
      >
        <div class="chip">{{ student.jurusan }}</div>
        {{ student.nim }}:{{ student.nama }}

        <router-link
          v-bind:to="{ name: 'view-student', params: { nim: student.nim } }"
          class="secondary-content"
        >
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data() {
    return {
      students: []
    };
  },
  created() {
    db.collection("student")
      .orderBy("jurusan")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          console.log(doc.id);
          const data = {
            id: doc.id,
            nim: doc.data().nim,
            nama: doc.data().nama,
            jurusan: doc.data().jurusan
          };
          this.students.push(data);
        });
      });
  }
};
</script>
