<template>
  <div id="new-student">
    <h3>New Student</h3>
    <div class="row">
      <form v-on:submit.prevent="save" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="nim" required />
            <label for="">NIM</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="jurusan" required />
            <label for="">Jurusan</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="nama" required />
            <label for="">Nama</label>
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cencel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "new-student",
  data() {
    return {
      nim: null,
      nama: null,
      jurusan: null
    };
  },
  methods: {
    save() {
      db.collection("student")
        .add({
          nim: this.nim,
          nama: this.nama,
          jurusan: this.jurusan
        })
        .then(function() {
          this.$router.push("/");
        })
        .catch(error => console.log(error));
    }
  }
};
</script>
