<template>
  <div id="edit-student">
    <h3>Edit Student</h3>
    <div class="row">
      <form v-on:submit.prevent="update" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input disabled type="text" v-model="nim" required />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="jurusan" required />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="nama" required />
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cencel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "edit-student",
  data() {
    return {
      nim: null,
      name: null,
      jurusan: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("student")
      .where("nim", "==", to.params.nim)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.nim = doc.data().nim;
            vm.nama = doc.data().nama;
            vm.jurusan = doc.data().jurusan;
          });
        });
      });
  },
  // fetch data dengan watcher
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("student")
        .where("nim", "==", this.$route.params.nim)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.nim = doc.data().nim;
            this.jurusan = doc.data().jurusan;
            this.nama = doc.data().nama;
          });
        });
    },
    update() {
      db.collection("student")
        .where("nim", "==", this.$route.params.nim)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref
              .update({
                nim: this.nim,
                jurusan: this.jurusan,
                nama: this.nama
              })
              .then(() => {
                this.$router.push({
                  name: "view-student",
                  params: { nim: this.nim }
                });
              })
              .catch(error => console.log(error));
          });
        });
    }
  }
};
</script>
